import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ToastrService } from 'ngx-toastr';
import { LoginService } from '../login/login.service';

declare var jQuery:any;

@Component({
  selector: 'forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.scss'],
  providers: [LoginService]
})
export class ForgetPasswordComponent implements OnInit {
  userEmail: string = '';
  busy: Promise<any>;

  constructor(
    private _router: Router,
    private _service: LoginService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.formHandling();
  }

  sendEmail() {
    this.busy = this._service.forgetPassword(this.userEmail).then(
      (res: any) => {
        this.toastr.success("Reset password link has been sent to your registered email");
      },
      (error) => {
        if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
          this.toastr.error(error.json().msg);
        } else {
          this.toastr.error('Unable reset your password. Please try later.');
        }
      }
    );
  }

   formHandling() {
        jQuery(document).ready(function() {
            function setHeight() {
                let windowHeight = jQuery(window).innerHeight();
                jQuery('#left').css('min-height', windowHeight);
                jQuery('#right').css('min-height', windowHeight);
                jQuery('.content').css('min-height', windowHeight);
            };
            
            setHeight();
            
          jQuery(window).resize(function() {
                setHeight();
            });
        });
    }

}



