import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { ToastrService } from 'ngx-toastr';
import { LoginService } from '../login/login.service';

declare var jQuery:any;

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css'],
  providers: [LoginService]
})
export class ResetPasswordComponent implements OnInit {
  user: any = {};
  token: string = '';
  error: string = '';
  busy: Promise<any>;
  passwordReset: boolean = false;

  constructor(
    public router: Router,
    private toastr: ToastrService,
    private activatedRoute: ActivatedRoute,
    private loginService: LoginService ) { }

  ngOnInit() {
    // subscribe to router event
    this.activatedRoute.params.subscribe((params: Params) => {
      if(params['token']) {
        this.token = params['token'];
      }
    });
    this.formHandling();
  }

  resetPassword() {
    this.busy = this.loginService.resetPassword(this.token, this.user).then((res: any) => {
        this.passwordReset = true;
    },(error) => {
        if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
          console.log(error.json().msg);
          this.error = error.json().msg;
        } else {
          this.error = 'Unable reset your password. Please try later.'
           this.toastr.error(error.message);
        }
    });
  }

  formHandling() {
        jQuery(document).ready(function() {
            function setHeight() {
                let windowHeight = jQuery(window).innerHeight();
                jQuery('#left').css('min-height', windowHeight);
                jQuery('#right').css('min-height', windowHeight);
                jQuery('.content').css('min-height', windowHeight);
            };
            
            setHeight();
            
          jQuery(window).resize(function() {
                setHeight();
            });
        });
    }

}



