import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { RouterModule } from '@angular/router';
import { LoginService } from './login.service';

import { ToastrService } from 'ngx-toastr';
declare var jQuery:any;

@Component({
  templateUrl: 'login.component.html',
  styleUrls: ['login.component.css'],
  providers: [LoginService]
})
export class LoginComponent implements OnInit {
    busy: Promise<any>;
    public user = { email: "", password: "" };
    public msg = '';
    public isForgotPassword: boolean = false;
    public emailNew: string;

    constructor(
        private _service: LoginService,
        private _router: Router,
        private toastr: ToastrService
    ) { }

    ngOnInit() {
        localStorage.removeItem('userID');
        localStorage.removeItem('token');
        localStorage.removeItem('email');
        localStorage.removeItem('name');
        this.formHandling();
    }

    login() {
    this.busy = this._service.login(this.user).then((res: any) => {
            let data = res.data;
            localStorage.setItem('userID', data._id);
            localStorage.setItem('isAdmin', data.is_admin);
            localStorage.setItem('token', res.token);
            localStorage.setItem('email', data.email);
            localStorage.setItem('name', data.fullName);
            localStorage.setItem('firstName', data.firstName);

            this.toastr.success(data.firstName, 'Welcome', { timeOut: 3000, });
            this._router.navigate(['/']);
        },(error) => {
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to login. Please try later.');
            }
        });
    }

    formHandling() {
        jQuery(document).ready(function() {
            function setHeight() {
                let windowHeight = jQuery(window).innerHeight();
                jQuery('#left').css('min-height', windowHeight);
                jQuery('#right').css('min-height', windowHeight);
                jQuery('.content').css('min-height', windowHeight);
            };
            setHeight();
            
            jQuery(window).resize(function() {
                setHeight();
            });
        });
    }

}



