import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Route } from '@angular/router';
import { RouterModule } from '@angular/router';
import { LoginService } from '../login/login.service';
import { User } from '../../_models/user';

import { ToastrService } from 'ngx-toastr';

declare var jQuery:any;
declare var Stripe:any;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers: [LoginService]
})
export class RegisterComponent implements OnInit {

    busy: Promise<any>;
    public user = new User();
    public cardNumber:any;
    public expMonth:any;
    public expYear:any;
    public CVC:any;

    constructor(
        private _service: LoginService,
        private _router: Router,
        private toastr: ToastrService
    ) { }

    ngOnInit() {
        this.cardNumber = 4242424242424242;
        this.expMonth = 12;
        this.expYear = 2022;
        this.CVC = 123;
        this.formHandling();
    }

    getToken() {

        this.busy = Stripe.card.createToken({
          number: this.cardNumber,
          exp_month: this.expMonth,
          exp_year: this.expYear,
          cvc: this.CVC
        }, (status: number, response: any) => {
            console.log("resssssssssss",status,response);
          if (status === 200) {
            this.user['token_id']= response.id;
            this.register();
            // this.message = `Success! Card token ${response.card.id}.`;
          } else {
              console.log("response.error.message",response.error.message)
            // this.toastr.error(response.error.message);
            this.toastr.error('Card Information is not correct.');
          }
        });
    }
    register() {
    this.busy = this._service.register(this.user).then((res: any) => {
            if(!res.error) {
                let data = res.data;
                this.toastr.success(res.msg, '', { timeOut: 3000, });
                this._router.navigate(['/login']);
            } else {
                this.toastr.error(res.msg);  
            }
        },(error) => {
            console.log("errrr-=-=-=-=-=-=-",error);
            if(error.headers._headers.get('content-type')[0] == "application/json; charset=utf-8") {
              this.toastr.error(error.json().msg);
            } else {
              this.toastr.error('you are not able to register. Please try later.');
            }
        });
    }

    formHandling() {
        jQuery(document).ready(function() {
            function setHeight() {
                let windowHeight = jQuery('#right').innerHeight();
                jQuery('#left').css('min-height', windowHeight);
                jQuery('#right').css('min-height', windowHeight);
                jQuery('.content').css('min-height', windowHeight);
            };
            
            setHeight();
            
        	jQuery(window).resize(function() {
                setHeight();
            });
        });
    }
}



